import React, { Component } from "react";
import styled from "styled-components";

const Div = styled.div`
  margin-bottom: 35px;
`;

const SubTitle = styled.h2`
  font-weight: normal;
  font-size: 32px;
`;

const Form = styled.form`
  width: 500px;
  margin: 0 auto;
  input {
    width: 100%;
    padding: 8px;
    border: 1px solid #e9e8e7;
    margin-bottom: 10px;
    outline: 0;
  }
`;

class LoginAndRegister extends Component {
  render() {
    return (
      <Div>
        <SubTitle>一、註冊行為</SubTitle>
        <Form>
          <div>
            <input type="text" />
          </div>

          <input type="password" />
          <button>登入</button>
        </Form>
      </Div>
    );
  }
}

export default LoginAndRegister;
